.footer {
	position: relative;
	width: 100%;
	margin-top: rem(28);

	&::before {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: rem(17);
		background-color: black;
	}

	&-top {
		display: flex;
		justify-content: space-between;
		margin-bottom: rem(32);

		@include min540 {
			flex-direction: column;
			align-items: center;
		}

		.link {
			font-size: rem(21);
			display: flex;
			align-items: center;

			@include min540 {
				margin-bottom: rem(12);
			}

			i {
				margin-right: rem(18);
			}
		}

		.right {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			span {
				margin-right: rem(28);
				font-size: rem(13);
			}

			span, a {
				color: #999999;
			}

			a {
				display: flex;
				align-items: center;
				font-size: rem(13);

				&:hover span {
					border-color: transparent;
				}

				i {
					font-size: rem(20);
					margin-right: rem(15);
				}
				span {
					padding-bottom: rem(4);
					border-bottom: rem(1) solid #999999;
				}
			}
		}
	}

	&-gallery {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: rem(52);
		

		img {
			width: 20%;
			flex: 0 0 20%;
		}
	}

	&-bottom {
		display: flex;
		flex-wrap: wrap;
		flex-direction: column;
		align-items: center;
		

		ul {
			width: 100%;
			display: flex;
			flex-wrap: wrap;
			border-bottom: rem(1) solid #ececec;
			padding-bottom: rem(50);
			margin-bottom: rem(28);
			justify-content: center;

			@include min540 {
				flex-direction: column;
				align-items: center;
			}

			li {
				margin-right: rem(60);

				@include min540 {
					margin-right: 0;
					margin-bottom: rem(10);
				}

				&:last-child {
					margin-right: 0;
				}

				a:hover {
					text-decoration: underline;
				}
			}
		}

		p {
			margin-bottom: rem(28);
			font-size: rem(13);
		}
	}
}