.nav {
	width: 100%;
	background-color: #478585;
	padding: rem(22) 0;
	display: none;

	ul {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;

		li {
			margin-right: rem(36);

			&:last-child {
				margin-right: 0;
			
			
			}

			a {
				color: #aece6e;

				&:hover {
					color: white;
				}
			}

		}
		li.active {
			a {
				color: white;
			}
		}
	}
}
.nav.active {
	display: block;
}

.speedbar {
	display: flex;
	flex-wrap: wrap;
	padding: rem(12) 0 rem(12) rem(130);

	color: #999999;
	font-size: rem(13);

	border-bottom: rem(1) solid #ececec;
	margin-bottom: rem(40);

	@include w768_w540 {
		padding: rem(12) rem(20) rem(12) rem(20);
		margin-bottom: rem(20);
	}

	@include min540 {
		padding: rem(12) rem(20) rem(12) rem(20);
		margin-bottom: rem(18);
	}

	a {
		display: block;
		position: relative;
		margin-right: rem(30);

		&:hover {
			text-decoration: underline;
		}

		&:last-child {
			margin-right: 0;

			&::before {
				display: none;
			}
		}

		&::before {
			pointer-events: none;
			content: "/";
			position: absolute;
			top: 0;
			right: rem(-18);
		}
	}
}