.page-article {

	h1,h2,h3 {
		font-weight: bold;
		font-size: rem(21);
		margin-bottom: rem(56);
	}

	p {
		margin-bottom: rem(25);

		&:last-of-type {
			margin-bottom: 0;
		}
	}

	.group {
		position: relative;
		display: flex;
		align-items: center;
		height: rem(480);
		margin: rem(40) 0;

		@include min540 {
			margin: rem(20) 0;
			height: rem(200);
		}

		span {
			font-size: rem(21);
			font-weight: bold;
			margin-left: rem(80);

			@include min540 {
				margin-left: rem(30);
				font-size: rem(17);
				max-width: 50%;
			}
		}
		

		img {
			position: absolute;
			top: 0;
			right: -10%;
			width: 100%;
			height: 100%;
			background-size: 80%;

			@include min540 {
				right: -15%;
				object-fit: contain;
			}

			@include min380 {
				right: -20%;
			}
		}
	}
}