.section-subs {
	position: relative;
	overflow: hidden;
	width: 100%;
	height: rem(346);
	background-color: #000;
	
	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		// display: block;
		background: url(../img/sub-bg.png) no-repeat left center;
		background-size: 67%;
		z-index: 0;

		@include min540 {
			background-size: cover;
		}
	}

	.container {
		display: flex;
		align-items: center;
		justify-content: flex-end;
		height: 100%;
	}
}

.subs-form {
	position: relative;
	z-index: 1;
	width: rem(360);
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;

	@include min540 {
		width: 100%;
	}

	.message {
		position: absolute;
		bottom: rem(-30);
		left: 0;
		color: white;
		font-size: rem(17);
		font-weight: bold;
	}

	.block-text {
		margin-bottom: rem(26);

		.title, .subtitle {
			display: block;
		}
		.title {
			font-size: rem(21);
			color: white;
			margin-bottom: rem(15);
		}
		.subtitle {
			color: #999999;
		}
	}

	.field {
		margin-bottom: rem(17);
	}
}

.field {
	width: 100%;
	background-color: #262626;
	padding: rem(20);
	color: #666666;
	font-size: rem(13);
	border: rem(1) solid #666666;

	@include placeholder {
		color: #666666;
	}
}

.button-green {
	font-size: rem(13);
	color: black;
	text-transform: uppercase;
	background-color: #aece6e;
	padding: rem(20) 0;
	width: 100%;
}