.article-page {
	position: relative;
	

	.container {
		display: flex;
		flex-wrap: wrap;
		padding-bottom: rem(35);
	}

	&-left {
		flex: 0 0 60%;
		display: flex;
		flex-wrap: wrap;

		@include w768_w540 {
			flex: 0 0 45%;
			flex-direction: column;
			align-self: flex-start;
			margin-right: rem(30);
		}

		@include min540 {
			flex: 0 0 100%;
			margin-right: 0;
			flex-direction: column;
			align-self: flex-start;
			margin-bottom: rem(20);
		}

		.image-nav {
			flex: 0 0 rem(75);
			margin-right: rem(24);

			@include w768_w540 {
				order: 10;
				margin-right: 0;
				margin-top: rem(10);
				flex: 0 0 rem(40);
				flex-wrap: wrap;
				display: flex;
				justify-content: space-between;
			}

			@include min540 {
				order: 10;
				margin-right: 0;
				margin-top: rem(10);
				flex: 0 0 auto;
				flex-wrap: wrap;
				display: flex;
				justify-content: space-between;
			}

			li {
				cursor: pointer;
				margin-bottom: rem(20);

				@include w768_w540 {
					margin-bottom: 0;
					margin-right: rem(10);
					width: 30%;
				}

				@include min540 {
					margin-bottom: 0;
					margin-right: rem(10);
					width: auto;

					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}

				&:last-child {
					margin-bottom: 0;

					@include w768_w540 {
						margin-right: 0;
					}

					@include min540 {
						margin-right: 0;
					}
				}
			}
		}

		.image-wrap {
			flex: 0 0 rem(520);
			// flex-grow: 1;

			@include w768_w540 {
				flex: 0 0 50%;
				height: rem(270);
			}

			@include min540 {
				flex: 0 0 100%;
				height: rem(320);
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}

	&-right {
		flex: 0 0 40%;
		display: flex;
		flex-wrap: wrap;
		flex-direction: column;

		@include w768_w540 {
			flex: 0 0 50%;
		}

		@include min540 {
			flex: 0 0 100%;
		}
		
		.title {
			font-size: rem(21);
			font-weight: bold;
			margin-bottom: rem(20);
			display: block;

			@include w768_w540 {
				margin-bottom: rem(10);
			}

			@include min540 {
				font-size: rem(24);
				margin-bottom: rem(10);
			}
		}

		.cat-list {
			display: flex;
			flex-wrap: wrap;
			margin-bottom: rem(32);

			@include w768_w540 {
				margin-bottom: rem(16);
			}

			@include min540 {
				margin-bottom: rem(10);
			}

			span {
				font-size: rem(13);
				color: #999;
				margin-right: rem(2);
				@include min540 {
					font-size: rem(17);
				}
			}
			a {
				font-size: rem(13);
				color: #dd7676;
				margin-right: rem(2);

				@include min540 {
					font-size: rem(17);
				}

				&:hover {
					text-decoration: underline;
				}

				&:last-child {
					margin-right: 0;
				}
			}
		}

		.article-page-reviews {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			margin-bottom: rem(58);

			@include w768_w540 {
				margin-bottom: rem(28);
			}

			@include min540 {
				margin-bottom: rem(20);
			}

			.control {
				display: flex;
				flex-wrap: wrap;
				overflow: hidden;
				flex-direction: row-reverse;
				margin-right: rem(18);

				&:not(:checked)>input {
					display: none;
				}

				&:not(:checked)>label {
					cursor: pointer;
					font-size: rem(20);
					color: #999;
					position: relative;
					padding-right: rem(5);

					&:hover,
					&:hover~label {
						color: #dd7676;

						&::before {
							content: "\f005";
							font-family: 'Font Awesome 5 Pro';
							font-weight: 900;
						}
					}

					&::before {
						content: "\f005";
						font-family: 'Font Awesome 5 Pro';
						font-weight: 300;
					}
				}

				>input:checked~label {
					color: #dd7676;

					&::before {
						content: "\f005";
						font-family: 'Font Awesome 5 Pro';
						font-weight: 900;
					}
				}

				>input:checked+label:hover,
				>input:checked+label:hover~label,
				>input:checked~label:hover,
				>input:checked~label:hover~label,
				>label:hover~input:checked~label {
					color: #dd7676;

					&::before {
						content: "\f005";
						font-family: 'Font Awesome 5 Pro';
						font-weight: 900;
					}
				}
			}

			
			a {
				color: #dd7676;
				display: flex;
				// align-items: center;

				@include min540 {
					font-size: rem(17);
				}

				&:hover {
					span {
						border-color: transparent;
					}
				}

				i {
					margin-right: rem(6);
				}

				span {
					font-size: rem(13);
					padding-bottom: rem(4);
					border-bottom: rem(1) solid #dd7676;

					@include min540 {
						font-size: rem(15);
					}
				}
			}
		}

		.list {
			display: flex;
			flex-wrap: wrap;
			flex-direction: column;
			margin-bottom: rem(46);

			@include w768_w540 {
				margin-bottom: rem(26);
			}

			.title {
				font-size: rem(17);
				font-weight: 400;
				margin-bottom: rem(24);

				@include w768_w540 {
					margin-bottom: rem(14);
				}
			}

			ul {
				li {
					margin-bottom: rem(28);
					display: flex;
					flex-wrap: wrap;

					@include min {
						margin-bottom: rem(18);
					}

					&:last-child {
						margin-bottom: 0;
					}

					span {
						color: #999;
						width: rem(62);
						// margin-right: rem(12);
					}
				}
			}
		}

		.tab {

			&-nav {
				padding: rem(20) rem(30);
				border: rem(1) solid #ececec;
				background-color: white;
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				margin-bottom: rem(24);

				@include w768_w540 {
					padding: rem(12) rem(10);
					margin-bottom: rem(18);
				}

				@include min540 {
					padding: rem(12) rem(10);
					margin-bottom: rem(18);
				}

				.btn {
					color: #aece6e;
					display: flex;
					align-items: center;
					margin-right: rem(90);

					@include ipad {
						margin-right: rem(15);
					}

					@include w768_w540 {
						margin-right: rem(30);
					}

					@include min540 {
						margin-right: rem(20);
					}

					&:last-child {
						margin-right: 0;
					}

					i {
						margin-right: rem(18);

						@include w768_w540 {
							margin-right: rem(8);
						}

						@include min540 {
							margin-right: rem(4);
						}
					}
				}
				.btn.active {
					color: black;
				}
			}
		}
	}
}

.block-border {
	width: 100%;
	padding: rem(20) rem(30);
	border: rem(1) solid #ececec;
	background-color: white;
	display: flex;
	align-items: center;
	justify-content: center;

	color: #478585;
	font-size: rem(13);
}