.catalog {
	
	hr {
		margin-bottom: rem(20);

		@include min540 {
			margin-bottom: rem(10);
		}
	}

	&-top {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-bottom: rem(25);

		@include min540 {
			flex-direction: column;
		}

		.left,.right {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			
		}
		.left {
			@include min540 {
				margin-bottom: rem(15);
			}
			.title {
				font-size: rem(21);
				font-weight: bold;
				margin-right: rem(24);

				@include w768_w540 {
					margin-right: rem(12);
					font-size: rem(20);
				}

				
			}
			.count {
				color: #999;
			}
		}

		.right {

			span {
				color: #999;
				font-size: rem(13);
				margin-right: rem(30);

				@include w768_w540 {
					margin-right: rem(15);
				}

				@include min540 {
					margin-right: rem(10);
				}
			}

			.custom-select {

				// @include w768_w540 {
				// 	width: rem(100);
				// }
			}
		}
	}

	&-wrap {
		position: relative;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		.aside {
			position: absolute;
			top: 0;
			right: 0;
			width: rem(270);

			@include min {
				display: none;
			}
		}
	}

	&-item {
		display: flex;
		flex-wrap: wrap;
		// width: 80%;
		width: 100%;
		// height: rem(800);
		
		.cart,
		.cart-accent {
			width: rem(270);
			margin-right: rem(30);
			margin-bottom: rem(30);
			align-self: flex-start;

			@include min {
				&:nth-child(4n) {
				    margin-right: 0;
			    }
			}

			@include w1200_w900 {
				width: rem(256);
			}

			// @include ipad {
			// 	width: rem(249);
			// }

			@include ipad {
				width: calc(25% - 16px);
			}

			@include w768_w540 {
				width: calc(33.333% - 14px);

				&:nth-child(4n) {
					margin-right: rem(30);
				}

				&:nth-child(3n) {
					margin-right: 0 !important;
				}
			}

			@include min540 {
				width: 100%;
				margin-right: 0;
			}
		}

		.cart {
			position: relative;
			height: auto;
			padding-bottom: rem(60);

			@include mw900 {
				&:nth-child(4),
				&:nth-child(7) {
					margin-right: rem(30);
				}

				&:nth-child(10) {
					margin-right: 0;
				}

				&:nth-child(14) {
					margin-right: 0;
				}

				// &:nth-child(8) {
				// 	margin-right: rem(30);
				// }
			}

			@include ipad {
				&:nth-child(4n) {
					margin-right: 0;
				}
			}

			@include w768_w540 {
				&:nth-child(3n) {
					margin-right: 0;
				}
			}

			

			.button-link {
				width: 100%;
				position: absolute;
				left: 0;
				bottom: 0;
				padding: rem(20) rem(30);
				
			}
		}

		.cart-accent {
			
		}
	}

	
}

.cart-accent {
	// order: 3;
	position: relative;
	padding: rem(20) rem(30);
	height: rem(750);
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	justify-content: flex-end;
	
	@include w1200_w900 {
		height: rem(720);
	}

	.image {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: -1;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	.control {

		.title, .subtitle, p {
			max-width: rem(175);
		}

		.title {
			font-size: rem(21);
			color: white;
			display: block;
			margin-bottom: rem(32);
			// max-width: rem(130) !important;
		}

		.subtitle {
			color: white;
			margin-bottom: rem(24);
			display: block;

			> span {
				margin-top: rem(2);
				color: #aece6e;
			}
		}

		p {
			font-size: rem(13);
			color: #999;
			margin-bottom: rem(42);
			display: block;
		}

		.button-link {
			width: 100%;
			padding: rem(20) rem(30);
		}
	}
}

.page-wrap {
	display: flex;
	flex-wrap: wrap;
	margin: rem(32) 0;

	ul {
		margin-left: auto;
		margin-right: rem(-50);

		display: flex;
		flex-wrap: wrap;

		li {
			margin-right: rem(9);

			a,span {
				font-size: rem(13);
				width: rem(18);
				padding-bottom: rem(4);
				display: block;
				text-align: center;
			}
			span {
				color: #999;
			}
			a {
				&:hover {
					border-color: transparent;
				}
			}
			a,
			.elips {
				color: #dd7676;
				border-bottom: rem(1) solid #dd7676;
			}
		}
	}

	> a {
		font-size: rem(13);
		text-transform: lowercase;
		margin-left: auto;
		color: #dd7676;
		border-bottom: rem(1) solid #dd7676;

		&:hover {
			border-color: transparent;
		}
	}
}