.article {
	padding: rem(32) rem(68);
	height: rem(300);
	position: relative;
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	justify-content: flex-end;

	@include min540 {
		padding: rem(14) rem(18);
	}

	&-image {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: -1;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&-control {
		display: flex;
		flex-wrap: wrap;
		flex-direction: column;

		.title, .subtitle {
			color: white;
		}

		.title {
			font-size: rem(21);
			margin-bottom: rem(20);
			
			@include min540 {
				margin-bottom: rem(10);
				font-size: rem(24);
			}
		}
		.subtitle {
			font-size: rem(13);
			margin-bottom: rem(22);

			@include min540 {
				font-size: rem(17);
			}
		}
	}
}

.cart {
	padding: rem(36) rem(26);
	height: rem(300);
	background-color: white;
	box-shadow: 0 0 rem(15) rgba(153,153,153,.3);
	display: flex;
	flex-wrap: wrap;

	@include w768_w540 {
		height: rem(260);
	}

	@include min540 {
		height: auto;
		padding: rem(18) rem(13);
	}

	&-control {
		position: relative;
		display: flex;
		flex-wrap: wrap;
		flex-direction: column;
		flex: 0 0 50%;
		justify-content: center;

		.image {
			position: absolute;
			top: 0;
			left: 0;
			width: rem(65);
			height: rem(65);
			border-radius: 50%;
			overflow: hidden;
			margin-bottom: rem(50);

			@include w768_w540 {
				width: rem(40);
				height: rem(40);
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				// flex: 0 0 rem(65);
			}
		}

		.image ~ .title {
			margin-top: rem(40);
		}
		

		.title {
			color: black;
			font-size: rem(21);
			margin-bottom: rem(16);

			@include min540 {
				font-size: rem(17);
				margin-bottom: rem(10);
			}

			@include min480 {
				font-size: rem(24);
			}
		}

		.subtitle {
			font-size: rem(13);
			margin-bottom: rem(22);
			color: #999999;

			@include min480 {
				font-size: rem(17);
			}
		}
	}

	&-image {
		margin-top: rem(15);
		flex: 0 0 50%;
		pointer-events: none;

		@include min {
			img {
				width: 65% !important;
				margin-left: rem(30);
			}
		}
	}
}

.reviews {
	// padding: rem(36) rem(26);
	height: rem(300);
	background-color: white;
	// box-shadow: 0 0 rem(15) rgba(153, 153, 153, .3);
	display: flex;
	flex-wrap: wrap;

	@include w768_w540 {
		height: rem(220);
	}

	@include min540 {
		height: rem(200);
	}

	&-left {
		position: relative;
		flex: 0 0 50%;
		padding-left: rem(70);
		display: flex;
		align-items: center;

		@include w768_w540 {
			flex: 0 0 40%;
		}

		@include min540 {
			padding-left: rem(20);
			flex: 0 0 40%;
		}

		&_image {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			// z-index: -1;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		&_control {
			position: relative;
			z-index: 1;
			display: flex;
			flex-wrap: wrap;
			flex-direction: column;

			.title,
			.subtitle {
				color: white;
			}

			.title {
				font-size: rem(21);
				margin-bottom: rem(20);

				@include min540 {
					margin-bottom: rem(10);
					font-size: rem(20);
				}
			}

			.subtitle {
				font-size: rem(13);
				// margin-bottom: rem(22);
			}
		}
	}

	&-right {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		flex: 0 0 50%;
		background-color: white;
		box-shadow: 0 0 rem(15) rgba(153, 153, 153, .3);

		padding: 0 rem(38);

		@include w768_w540 {
			padding: 0 rem(14);
			flex: 0 0 60%;
		}

		@include min540 {
			padding: 0 rem(10);
			flex: 0 0 60%;
		}

		.avatar {
			width: rem(100);
			height: rem(100);
			flex: 0 0 rem(100);
			border-radius: 100%;
			overflow: hidden;
			margin-right: rem(32);
			margin-top: rem(74);
			margin-bottom: auto;

			@include w768_w540 {
				width: rem(60);
				height: rem(60);
				flex: 0 0 rem(60);
				margin-right: rem(16);
				margin-top: rem(54);
			}

			@include min540 {
				width: rem(40);
				height: rem(40);
				flex: 0 0 rem(40);
				margin-right: rem(10);
				margin-top: rem(30);
			}
		}

		.info {
			display: flex;
			flex: 0 0 65%;
			flex-wrap: wrap;
			// margin-top: rem(25);

			.top {
				display: flex;
				flex-wrap: wrap;
				margin-bottom: rem(35);
				width: 100%;

				@include min540 {
					margin-bottom: rem(10);
				}

				span {
					color: #999999;
				}

				ul {
					display: flex;
					flex-wrap: wrap;
					margin-left: auto;
					margin-right: rem(40);

					@include min540 {
						margin-right: rem(15);
						margin-left: 0;
					}

					li {
						margin-right: rem(8);

						@include min540 {
							margin-right: rem(4);
						}

						&:last-child {
							margin-right: 0;
						}

						i {
							color: #dd7676;
						}
					}
				}
			}

			p {
				position: relative;
				font-size: rem(21);
				font-style: italic;
				padding-left: rem(20);

				@include w768_w540 {
					font-size: rem(17);
				}

				@include min540 {
					font-size: rem(16);
				}

				&::before {
					content: "\f105";
					font-family: 'Font Awesome 5 Pro';
					font-weight: 900;
					position: absolute;
					top: 0;
					left: 0;
				}
			}
		}
	}

}

#carousel-cart,
#reviews-cart {
	.owl-stage-outer {
		margin: rem(-10);
		padding: rem(10);
	}
}

.block-carousel {
	margin-top: rem(70);
	padding-bottom: rem(58);
	margin-bottom: rem(48);
	display: none;
	// opacity: 0;

	@include min540 {
		margin-top: rem(24);
		padding-bottom: rem(28);
		margin-bottom: rem(18);
	}

	&_header {
		display: flex;
		flex-wrap: wrap;
		// align-self: flex-start;
		align-items: center;
		justify-content: space-between;
		margin-bottom: rem(34);

		@include min540 {
			margin-bottom: rem(14);
			flex-direction: column;
			align-items: flex-start;
		}
	}

	h1,h2,h3 {
		font-size: rem(21);
		color: black;
		font-weight: bold;

		@include min540 {
			margin-bottom: rem(8);
		}
	}

	.link {
		display: block;
		font-size: rem(13);
		border-bottom: rem(1) solid #dd7676;
		padding-bottom: rem(4);
		color: #dd7676;
		&:hover {
			border-color: transparent;
		}
	}

	.owl-nav {
		position: absolute;
		height: rem(117);
		display: flex;
		align-items: center;
		background-color: #478585;
		width: 100%;
		bottom: rem(64);
		z-index: -1;

		@include min540 {
			background-color: transparent;
		}

		// @include min {
		// 	height: rem(40);
		// 	bottom: rem(-80);
		// }

		.owl-prev,
		.owl-next {
			position: absolute;
			top: 0;
			width: rem(60);
			height: 100%;
			background-color: #478585 !important;

			@include min {
				width: rem(40);
				height: rem(40);
				top: auto;
				bottom: rem(-80);
			}

			i {
				color: white;
				font-size: rem(22);
			}
		}
		.owl-prev {
			left: rem(-60);

			@include min {
				left: 0;
			}
		}
		.owl-next {
			right: rem(-60);

			@include min {
				right: 0;
			}
		}
	}

	.owl-dots {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		margin-top: rem(34);

		@include min {
			width: 80%;
			margin-left: 10%;
			z-index: -2;
		}

		.owl-dot {
			width: rem(80);
			height: rem(4);
			background-color: #aece6e;
			margin-right: rem(30);
			&:last-child {
				margin-right: 0;
			}

			@include w768_w540 {
				width: rem(40);
				margin-right: rem(15);
			}

			@include min540 {
				width: rem(25);
				margin-right: rem(8);
			}

		}
		.owl-dot.active {
			background-color: #488585;
		}
	}
}

.block-carousel.active {
	display: block;
}

.animateHide {
	animation: hide 1s forwards;
}

@keyframes hide {
	0% {
		opacity: .5;
	}

	50% {
		opacity: 0;
	}

	100% {
		display: none;
	}
}