.header {
	position: relative;
	width: 100%;
	// background-color: black;
	// height: rem(644);

	.container {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
	}

	&::before {
		content: "";
		transition: all 500ms ease;
	}

	&-block {
		margin-top: rem(300);
		width: 50%;
		align-self: flex-end;

		@include w768_w540 {
			width: 65%;
		}

		@include min540 {
			margin-top: rem(140);
			padding-left: rem(30);
			width: 75%;
			align-self: baseline;
		}

		// @include min380 {
		// 	width: 62%;
		// 	padding-left: rem(10);
		// }

		.tab {
			display: none;
		}
		.tab.active {
			display: block;
		}
		&_title {
			font-size: rem(83);
			line-height: rem(72);
			margin-bottom: rem(50);
			color: white;

			@include min540 {
				font-size: rem(38);
				line-height: rem(38);
				margin-bottom: rem(20);
			}

			@include min380 {
				font-size: rem(30);
				line-height: rem(30);
				width: 64%;
			}

			span {
				display: block;
			}
			.black {
				color: black;
			}
		}

		&_link {
			font-size: rem(13);
			color: white;
			text-transform: uppercase;
			display: flex;
			align-items: center;

			i {
				margin-right: rem(14);
			}
		}
	}

	&-bottom {
		display: flex;
		flex-wrap: wrap;
		flex-direction: column;
		margin-top: rem(52);
		width: 100%;

		@include min540 {
			margin-top: rem(20);
		}

		.top {
			display: flex;
			justify-content: center;

			.line {
				&:first-child {
					margin-right: rem(30);
				}
			}
		}

		.control {
			display: flex;
			justify-content: space-between;
			margin-top: rem(16);
			width: 100%;
			align-self: flex-start;
			border-bottom: 1px solid #ececec;

			@include min540 {
				flex-wrap: wrap;
				padding-bottom: rem(14);
			}

			.left,.right {
				margin-top: rem(100);
				flex: 0 0 25%;

				@include ipad {
					margin-top: rem(70);
				}

				@include w768_w540 {
					margin-top: rem(48);
				}

				@include min540 {
					margin-top: rem(20);
					flex: 0 0 50%;
				}

				i {
					font-size: rem(18);
				}
			}
			.left {
				i {
					margin-right: rem(40);

					@include w768_w540 {
						margin-right: rem(10);
					}

					@include min540 {
						margin-right: rem(10);
					}
				}
				.title {
					color: #999999;

					@include w768_w540 {
						font-size: rem(14);
					}
				}
			}
			.right {
				justify-self: flex-end;
				text-align: right;
			}
			.center {
				display: flex;
				flex-wrap: wrap;

				@include min540 {
					order: -1;
					margin-left: auto;
					margin-right: auto;
				}

				img {
					transition: all 500ms ease;

					@include w1200_w900 {
						width: 70%;
					}

					@include min {
						width: 70%;
					}

					@include min540 {
						width: 60%;
					}

					@include min480 {
						width: 50%;
					}
				}
			}
		}
	}

	&-button {
		position: relative;
		width: rem(255);
		height: rem(157);
		background-color: white;
		display: flex;
		align-items: center;
		justify-content: center;

		@include ipad {
			width: rem(180);
			height: rem(100);
		}

		@include w768_w540 {
			width: rem(120);
			height: rem(70);
		}

		@include min540 {
			width: 50%;
			height: rem(75);
		}
		
	}
	&-button.green {
		background-color: $green;
		&::before {
			content: "\f078";
			font-family: 'Font Awesome 5 Pro';
			font-weight: 900;
			position: absolute;
			bottom: rem(-34);
			color: black;

			@include min540 {
				display: none;
			}
		}
	}
}

.line {
	width: rem(80);
	height: rem(4);
	background-color: #aece6e;
}
.line.active {
	background-color: white;
}

.header.first {
	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		width: 100%;
		height: rem(644);
		background: url(../img/lusso-header.jpg) no-repeat right center;
		background-size: cover;

		@include ipad {
			height: rem(620);
		}

		@include w768_w540 {
			height: rem(600);
		}

		@include min540 {
			background: url(../img/lusso-header.jpg) no-repeat top center;
			height: rem(300);
		}
		
	}
}
.header.last {
	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		width: 100%;
		height: rem(644);
		background: url(../img/diva-header.jpg) no-repeat right center;
		background-size: cover;

		@include ipad {
			height: rem(620);
		}

		@include w768_w540 {
			height: rem(600);
		}

		@include min540 {
			background: url(../img/diva-header.jpg) no-repeat top center;
			height: rem(300);
		}
	}
}
.header.small {
	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		width: 100%;
		height: rem(90);
		background-color: black;
		background-image: none;

		@include w768_w540 {
			height: rem(70);
		}
	}

	.header-bottom {
		margin-top: rem(14);
	}
	.header-bottom .control {
		border-bottom: 0;
	}
	.header-button.green::before {
		display: none;
	}
}