
$green: #478585;

* {
	outline: none;
	border: 0;
}

html {
	font-size: 100%;

	@include max {
		font-size: 130%;
	}

	@include w1200_w900 {
		font-size: 80%;
	}

	@include min {
		font-size: 68%;
	}

	@include min540 {
		font-size: 100%;
	}

	@include min380 {
		font-size: 80%;
	}
}

button {
	background-color: transparent;
}

a {
	text-decoration: none;
}

li {
	list-style: none;
}

body {
	font-family: 'Cuprun', sans-serif;
	font-weight: 400;
	font-size: rem(17);
	// overflow-x: hidden;
}

.relative {
	position: relative;
}

.container {
	width: 1170px;
	margin: 0 auto;

	@include max {
		width: 1460px;
	}

	@include w1200_w900 {
		width: 900px;
	}

	@include min {
		width: calc(100% - 40px);
	}
}
.container-big {
	width: 1280px;
	margin: 0 auto;

	@include max {
		width: 1460px;
	}

	@include w1200_w900 {
		width: 980px;
	}

	@include min {
		width: 100%;
		// padding-left: rem(20);
		// padding-right: rem(20);
	}
}

hr {
	width: 1280px;
	margin: 0 auto;
	border-bottom: rem(1) solid #ececec;
}

.button-link {
	display: flex;
	align-items: center;
	justify-content: space-between;
	background-color: #aece6e;
	padding: rem(20) rem(40) rem(20) rem(60);
	width: rem(263);
	transition: all 500ms ease;

	@include min540 {
		width: rem(210);
		padding: rem(18) rem(20) rem(18) rem(30) !important;
	}

	@include min480 {
		padding: rem(14) rem(16) rem(14) rem(18) !important;
	}

	@include min380 {

	}

	&:hover {
		i {
			position: relative;
			right: rem(-10);
		}
	}

	span {
		font-size: rem(13);
		text-transform: uppercase;
	}
	i {
		position: relative;
		right: 0;
		font-size: rem(13);
		transition: all 500ms ease;
	}
}


.mt-35 {
	margin-top: rem(35);
}
.mb-35 {
	margin-bottom: rem(35);
}
.my-35 {
	margin: rem(35) 0;
}

.px-30 {
	padding-left: rem(30);
	padding-right: rem(30);
}
.py-20 {
	padding-top: rem(20);
	padding-bottom: rem(20);
}

.w-32 {
	width: rem(320) !important;
}

.w-100 {
	width: 100%;
}
.h-100 {
	height: 100%;
}

.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-center {
	justify-content: center;
}

.mr-item-12 {
	
	> div, > a {
		margin-right: rem(12);

		&:last-child {
			margin-right: 0;
		}
	}

	
}

.image-fluid {
	width: 100%;

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}